<template>
  <hr class="d-block hr-space-nomargin" />
  <div style="height: 100%; display: flex; flex-flow: column nowrap">
    <div style="padding: 0.5rem; background: #fff">
      <el-input
        v-if="false"
        style="width: 280px; margin-bottom: 0.5rem; margin-right: 1rem"
        v-model="search"
        placeholder="Please input"
        class="input-with-select"
      >
        <!--   <template #prepend>
          <el-select v-model="select" placeholder="Select" style="width: 110px">
            <el-option label="Restaurant" value="1"></el-option>
            <el-option label="Order No." value="2"></el-option>
            <el-option label="Tel" value="3"></el-option>
          </el-select>
        </template> -->
        <template #append>
          <el-button type="primary" v-loading="false">搜索</el-button>
        </template>
      </el-input>

      <el-button
        style="margin-right: 1rem; margin-bottom: 0.1rem; margin-left: 0px"
        type="primary"
        size="mini"
        @click="showCreateAlbumDialog"
        v-loading="false"
        >新建相册</el-button
      >
      <el-button
        @click="showUploadPhotoDialog"
        style="margin-right: 1rem; margin-bottom: 0.1rem; margin-left: 0px"
        type="primary"
        size="mini"
        v-loading="false"
        >上传图片</el-button
      >
    </div>
    <hr class="d-block hr-space-nomargin" />
    <el-scrollbar v-loading="albums.loading" style="margin-bottom: 36px">
      <el-row>
        <el-col
          :xs="{ span: 12 }"
          v-for="(item, index) in albums.data"
          :key="index"
          :span="8"
          :lg="{ span: 6 }"
        >
          <ItemImg :item="item" />
        </el-col>
      </el-row>
    </el-scrollbar>
  </div>

  <AddAlbum ref="albumcreate" :maxOrder="maxOrder" />
  <ImgCopper ref="copper" />
  <UploadImg ref="photocreate" />
</template>

<script>
import ItemImg from "../components/item_img.vue";
import AddAlbum from "../components/dialog-add-album.vue";
import ImgCopper from "../components/dialog-img-copper.vue";
import UploadImg from "../components/dialog-upload-img.vue";
import { urls, http } from "../http/index.js";
import { ElMessage } from "element-plus";

export default {
  name: "Album",
  components: {
    ItemImg,
    AddAlbum,
    ImgCopper,
    UploadImg,
  },
  data() {
    return {
      value: "",
      search: "",
      maxOrder: -1,
      albums: { loading: false, page: 1, pageSize: 20, data: [], count: 0 },
    };
  },
  mounted() {
    this.getAlbumListHttp();
  },

  methods: {
    selectLabel(value) {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(function () {
        console.dir(value);
        // todo 搜索
      }, 1000);
    },
    showCreateAlbumDialog() {
      // this.$refs.albumcreate.showDialog(this.onAlbumCreated, this.$refs.copper);
      this.$refs.albumcreate.showDialog(this.createAlbumHttp);
      this.getMaxOrderHttp();
    },
    showUploadPhotoDialog() {
      this.$refs.photocreate.showDialog(null);
    },
    getMaxOrderHttp() {
      http.httpGet(
        (result) => {
          if (result.code == 0) {
            this.maxOrder = result.data.order;
          } else {
            this.maxOrder = 1;
            // ElMessage.error(result.message);
          }
        },
        (err) => {
          ElMessage.error(err);
        },
        urls.path.album.maxOrder
      );
    },
    createAlbumHttp(result, callback) {
      const forms = new FormData();
      forms.append("cover", result.file.raw);
      forms.append("name", result.name);
      forms.append("description", result.description);
      forms.append("order", result.order);
      forms.append("status", 0);
      http.httpPostData(
        (result) => {
          callback();
          if (result.code == 0) {
            ElMessage.success(result.message);
          } else {
            ElMessage.error(result.message);
          }
        },
        (err) => {
          callback();
          ElMessage.error(err);
        },
        urls.path.album.create,
        forms
      );
    },
    getAlbumListHttp() {
      this.albums.loading = true;
      http.httpPostBody(
        (result) => {
          this.albums.loading = false;
          if (result.code == 0) {
            this.albums.data = result.data.data;
            for (var i = 0; i < this.albums.data.length; i++) {
              this.albums.data[i].album = true;
            }
            this.albums.count = result.data.count;
          } else {
            ElMessage.error(result.message);
          }
        },
        (err) => {
          this.albums.loading = false;
          // this.search = err;
          ElMessage.error(err.message);
        },
        urls.path.album.list,
        { page: this.albums.page, pageSize: this.albums.pageSize }
      );
    },
  },
};
</script>

<style>
@media (max-width: 400px) {
  .cardCol {
     {
      span: 24;
    }
  }
}
</style>
