<template>
  <!-- ces -->
  <el-dialog width="320px" v-model="show" title="新建相册" :show-close="false">
    <el-form :model="actionFrom" label-position="left">
      <el-form-item label="名称:">
        <el-input
          v-model="actionFrom.name"
          placeholder="名称"
          clearable
          show-word-limit
          maxlength="20"
        />
      </el-form-item>

      <el-form-item label="封面:" class="title">
        <div class="upload-border">
          <el-upload
            class="avatar-uploader"
            action="/"
            accept="image/jpg, image/jpeg, image/png, image/gif"
            :auto-upload="false"
            :show-file-list="false"
            :on-change="selectFile"
          >
            <img
              v-if="actionFrom.cover"
              :src="actionFrom.cover"
              class="avatar"
            />

            <!-- style="border-radius: 50%" -->
            <i v-else class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </div>
      </el-form-item>

      <el-form-item label="描述:">
        <el-input
          type="textarea"
          v-model="actionFrom.description"
          placeholder="描述"
          clearable
          rows="3"
          show-word-limit
          maxlength="120"
        />
      </el-form-item>

      <el-form-item label="排序:">
        <el-input-number v-model="actionFrom.order" :min="1" :max="999" />
        <br />
        <span v-if="maxOrder > 0" style="font-size: 9px; color: #999">
          {{ "数值越大，排序越靠前，当前最大(" + maxOrder + ")" }}</span
        >
      </el-form-item>
      <!-- <el-form-item label="状态:">
        <el-select
          @change="selectLabel"
          style="width: 120px"
          v-model="value"
          placeholder="展示"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item> -->
    </el-form>

    <div style="text-align: right">
      <el-button size="mini" @click="show = false">取消</el-button>

      <el-button
        size="mini"
        type="primary"
        @click="confirmAction"
        v-loading="actionFrom.loading"
        >创建</el-button
      >
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: "AddAlbum",
  data() {
    return {
      actionFrom: { loading: false, order: 1, name: "", description: "" },
      show: false,
      onAlbumCreated: null,
      copper: null,
    };
  },
  props: {
    maxOrder: {
      type: Number,
      default: -1,
    },
  },
  methods: {
    confirmAction() {
      // upload
      this.actionFrom.loading = true;
      if (this.onAlbumCreated) {
        this.onAlbumCreated(this.actionFrom, (result) => {
          this.actionFrom.loading = false;
          this.show = false;
        });
      }
    },
    selectFile(file, fileList) {
      const ss = file.name.split(".");
      const sufix = ss[ss.length - 1] || "";
      if (!["jpg", "jpeg", "png"].includes(sufix)) {
        return false;
      }
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        this.$message.error("file size should less than 5MB!");
        return false;
      }
      const reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = () => {
        const img = String(reader.result);
        if (this.copper) {
          this.copper.show(img, this.onCopper);
        } else {
          this.actionFrom.cover = img;
          this.actionFrom.file = file;
        }
      };
    },

    onCopper(result, oriData) {
      this.actionFrom.cover = result;
      const aTime = new Date().getTime(); //
      const fileName = aTime + "." + oriData.type.substr(6); //
      const file = new File([oriData], fileName, {
        type: oriData.type,
      });
      this.actionFrom.file = file;
    },
    showDialog(callback, copper) {
      this.show = true;
      this.copper = copper;
      this.onAlbumCreated = callback;
      this.actionFrom.loading = false;
    },
  },
};
</script>
<style>
/* ---dialog */
.upload-border {
  padding: 1px;
  width: 124px;
  min-height: 124px;
  max-height: 284px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.upload-border:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  max-height: 280px;
  min-height: 120px;
  text-align: center;
}
.avatar {
  width: 120px;
  max-height: 280px;
  min-height: 120px;
  display: block;
  object-fit: contain;
}
</style>
